// generated by gong
import { Component, OnInit, AfterViewInit, ViewChild, Inject, Optional } from '@angular/core';
import { BehaviorSubject } from 'rxjs'
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatButton } from '@angular/material/button'

import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog'
import { DialogData } from '../front-repo.service'
import { SelectionModel } from '@angular/cdk/collections';

const allowMultiSelect = true;

import { Router, RouterState } from '@angular/router';
import { DependencyDB } from '../dependency-db'
import { DependencyService } from '../dependency.service'

import { FrontRepoService, FrontRepo } from '../front-repo.service'

// generated table component
@Component({
  selector: 'app-dependencys-table',
  templateUrl: './dependencys-table.component.html',
  styleUrls: ['./dependencys-table.component.css'],
})
export class DependencysTableComponent implements OnInit {

  // used if the component is called as a selection component of Dependency instances
  selection: SelectionModel<DependencyDB>;
  initialSelection = new Array<DependencyDB>();

  // the data source for the table
  dependencys: DependencyDB[];
  matTableDataSource: MatTableDataSource<DependencyDB>


  // front repo, that will be referenced by this.dependencys
  frontRepo: FrontRepo

  // displayedColumns is referenced by the MatTable component for specify what columns
  // have to be displayed and in what order
  displayedColumns: string[];

  // for sorting & pagination
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  ngAfterViewInit() {
    this.matTableDataSource.sort = this.sort;
    this.matTableDataSource.paginator = this.paginator;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.matTableDataSource.filter = filterValue.trim().toLowerCase();
  }

  constructor(
    private dependencyService: DependencyService,
    private frontRepoService: FrontRepoService,

    // not null if the component is called as a selection component of dependency instances
    public dialogRef: MatDialogRef<DependencysTableComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public dialogData: DialogData,

    private router: Router,
  ) {
    // https://stackoverflow.com/questions/54627478/angular-7-routing-to-same-component-but-different-param-not-working
    // this is for routerLink on same component when only queryParameter changes
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };

    // observable for changes in structs
    this.dependencyService.DependencyServiceChanged.subscribe(
      message => {
        if (message == "post" || message == "update" || message == "delete") {
          this.getDependencys()
        }
      }
    )
    if (dialogData == undefined) {
      this.displayedColumns = ['ID', 'Edit', 'Delete', // insertion point for columns to display
        "Name",
        "Task",
        "Dependencies",
      ]
    } else {
      this.displayedColumns = ['select', 'ID', // insertion point for columns to display
        "Name",
        "Task",
        "Dependencies",
      ]
      this.selection = new SelectionModel<DependencyDB>(allowMultiSelect, this.initialSelection);
    }

  }

  ngOnInit(): void {
    this.getDependencys()
    this.matTableDataSource = new MatTableDataSource(this.dependencys)
  }

  getDependencys(): void {
    this.frontRepoService.pull().subscribe(
      frontRepo => {
        this.frontRepo = frontRepo
        console.log("front repo pull returned")

        this.dependencys = this.frontRepo.Dependencys_array;

        // insertion point for variables Recoveries

        // in case the component is called as a selection component
        if (this.dialogData != undefined) {
          this.dependencys.forEach(
            dependency => {
              let ID = this.dialogData.ID
              let revPointer = dependency[this.dialogData.ReversePointer]
              if (revPointer.Int64 == ID) {
                this.initialSelection.push(dependency)
              }
            }
          )
          this.selection = new SelectionModel<DependencyDB>(allowMultiSelect, this.initialSelection);
        }

        // update the mat table data source
        this.matTableDataSource.data = this.dependencys
      }
    )
  }

  // newDependency initiate a new dependency
  // create a new Dependency objet
  newDependency() {
  }

  deleteDependency(dependencyID: number, dependency: DependencyDB) {
    // list of dependencys is truncated of dependency before the delete
    this.dependencys = this.dependencys.filter(h => h !== dependency);

    this.dependencyService.deleteDependency(dependencyID).subscribe(
      dependency => {
        this.dependencyService.DependencyServiceChanged.next("delete")

        console.log("dependency deleted")
      }
    );
  }

  editDependency(dependencyID: number, dependency: DependencyDB) {

  }

  // display dependency in router
  displayDependencyInRouter(dependencyID: number) {
    this.router.navigate(["dependency-display", dependencyID])
  }

  // set editor outlet
  setEditorRouterOutlet(dependencyID: number) {
    this.router.navigate([{
      outlets: {
        editor: ["dependency-detail", dependencyID]
      }
    }]);
  }

  // set presentation outlet
  setPresentationRouterOutlet(dependencyID: number) {
    this.router.navigate([{
      outlets: {
        presentation: ["dependency-presentation", dependencyID]
      }
    }]);
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dependencys.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dependencys.forEach(row => this.selection.select(row));
  }

  save() {

    let toUpdate = new Set<DependencyDB>()

    // reset all initial selection of dependency that belong to dependency through Anarrayofb
    this.initialSelection.forEach(
      dependency => {
        dependency[this.dialogData.ReversePointer].Int64 = 0
        dependency[this.dialogData.ReversePointer].Valid = true
        toUpdate.add(dependency)
      }
    )

    // from selection, set dependency that belong to dependency through Anarrayofb
    this.selection.selected.forEach(
      dependency => {
        console.log("selection ID " + dependency.ID)
        let ID = +this.dialogData.ID
        dependency[this.dialogData.ReversePointer].Int64 = ID
        dependency[this.dialogData.ReversePointer].Valid = true
        toUpdate.add(dependency)
      }
    )

    // update all dependency (only update selection & initial selection)
    toUpdate.forEach(
      dependency => {
        this.dependencyService.updateDependency(dependency)
          .subscribe(dependency => {
            this.dependencyService.DependencyServiceChanged.next("update")
            console.log("dependency saved")
          });
      }
    )
    this.dialogRef.close('Pizza!');
  }
}
